<template>
<div>
  <div class="content__top content__top--catalog">
    <h1 class="content__title">
      Каталог
    </h1>
    <span class="content__info">
      152 товара
    </span>
  </div>
  <div class="content__catalog">
       <product-filter :filterPriceTo.sync="filterPriceTo" :filterPriceFrom.sync="filterPriceFrom" 
          :filterCategoryId.sync="filterCategoryId" :filterColorId.sync="filterColorId" />

      <section class="catalog">
        <div v-if="productLoading">Загрузка списка товаров...</div>
        <div style="color: red;" v-if="productLoadingFailed">Произошла ошибка при загрузке! <button @click.prevent="loadProducts">Попробовать ещё раз</button></div>
        <product-list :products="products" />
        <v-pagination v-model="page" :total="totalProducts" :perPage="productsPerPage"  />       
      </section>    
      
  </div>
</div>    
</template>

<script>

import ProductList from '@/components/ProductList.vue';
// import products from '@/data/products';
import VPagination from '@/components/VPagination.vue';
import ProductFilter from '@/components/ProductFilter.vue';
import axios from 'axios';
import { API_BASE_URL } from '@/config';

export default {
  components: {
    ProductList,
    VPagination,
    ProductFilter
  },
  data() {
    return {
      page: 1,
      productsPerPage: 3,
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: 0,
      filterColorId: 0,
      productsData: null,
      productLoading: false,
      productLoadingFailed: false
    };
  },
  computed: {
    // filteredProducts() {
    //   let filtered = products;

    //   if (this.filterPriceFrom > 0) {
    //     filtered = filtered.filter(p => p.price > this.filterPriceFrom);
    //   }

    //   if (this.filterPriceTo > 0) {
    //     filtered = filtered.filter(p => p.price < this.filterPriceTo);
    //   }

    //   if (this.filterCategoryId > 0) {
    //     filtered = filtered.filter(p => p.categoryId === this.filterCategoryId);
    //   }

    //   return filtered;
    // },
    products() {
      //const offset = (this.page - 1) * this.productsPerPage;
      //return this.filteredProducts.slice(offset, offset + this.productsPerPage);
      return this.productsData ? this.productsData.items.map(product => {
        return {
          ...product,
          image: product.image.file.url
        }
      }) : [];
    },
    totalProducts() {
      //return this.filteredProducts.length;
      return this.productsData?.pagination.total ?? 0;
    }
  },
  methods: {
    loadProducts() {
      clearTimeout(this.timerId);
      this.productLoading = true;
      this.productLoadingFailed = false;

      this.timerId = setTimeout(() => {
        axios.get(`${API_BASE_URL}/api/products`, {
        params: {
          page: this.page,
          limit: this.productsPerPage,
          minPrice: this.filterPriceFrom,
          maxPrice: this.filterPriceTo,
          categoryId: this.filterCategoryId,
          colorId: this.filterColorId
        }
      })
      .then(response => this.productsData = response.data)
      .catch(() => this.productLoadingFailed = true)
      .then(() => this.productLoading = false);
      }, 0);
    }
  },
  created() {
    this.loadProducts();
  },
  watch: {
    page() {
      this.loadProducts();
    },
    filterPriceFrom() {
      this.loadProducts();
    },
    filterPriceTo() {
      this.loadProducts();
    },
    filterCategoryId() {
      this.loadProducts();
    },
    filterColorId() {
      this.loadProducts();
    }
  }
}

</script>
