<template>
     <ul class="catalog__pagination pagination">
          <li class="pagination__item">
            <a class="pagination__link pagination__link--arrow" aria-label="Предыдущая страница"
                :class="{'pagination__link--disabled': page === 1}" @click="prev(page)">
              <svg width="8" height="14" fill="currentColor">
                <use xlink:href="#icon-arrow-left"></use>
              </svg>
            </a>
          </li>
          <li class="pagination__item" v-for="pageNumber in pages" :key="pageNumber">
            <a href="#" class="pagination__link" :class="{'pagination__link--current': page === pageNumber}" 
                @click.prevent="paginate(pageNumber)">
              {{pageNumber}}
            </a>
          </li>          
          <li class="pagination__item">
            <a class="pagination__link pagination__link--arrow" aria-label="Следующая страница"
                :class="{'pagination__link--disabled': page === pages}" @click="next(page)">
              <svg width="8" height="14" fill="currentColor">
                <use xlink:href="#icon-arrow-right"></use>
              </svg>
            </a>
          </li>
        </ul>
</template>
<script>
export default {
    model: {
        prop: 'page',
        event: 'paginate'
    },
    props: ['page', 'total', 'perPage'],
    computed: {
        pages() {
            return Math.ceil(this.total / this.perPage);
        }
    },
    methods: {
        paginate(page) {
            this.$emit('paginate', page);
        },
        prev(page) {
            this.$emit('paginate', page > 1 ? page - 1 : page);
        },
        next(page) {
            this.$emit('paginate', page < this.pages ? page + 1 : page);
        }
    }
}
</script>



