<template>
    <li class="cart__item product">
              <div class="product__pic">
                <img :src="item.product.image" width="120" height="120" :alt="item.product.title">
              </div>
              <h3 class="product__title">
                {{item.product.title}}
              </h3>              
              <span class="product__code">
                Артикул: {{item.productId}}
              </span>

              <product-amount-control :productAmount.sync="amount" :size="10" />

              <b class="product__price">
                {{item.product.price | numberFormat}} ₽
              </b>

              <button class="product__del button-del" type="button" aria-label="Удалить товар из корзины" @click.prevent="deleteItem(item.productId)">
                <svg width="20" height="20" fill="currentColor">
                  <use xlink:href="#icon-close"></use>
                </svg>
              </button>
            </li>
</template>

<script>
import numberFormat from '@/helpers/numberFormat'
import { mapMutations, mapActions } from 'vuex'
import ProductAmountControl from '@/components/ProductAmountControl.vue'

export default {
    filters: {
      numberFormat
    },
    components: {
      ProductAmountControl
    },
    props: ['item'],
    computed: {
        amount: {
            get() {
                return this.item.amount;
            },
            set(value) {
                this.$store.dispatch('updateCartProductAmount', { productId: this.item.productId, amount: value });
            }
        }
    },
    methods: {
        //...mapMutations({deleteItem: 'deleteCartProduct'}),
        ...mapActions({deleteItem: 'deleteCartProduct'})
    }
}
</script>