import BaseFormField from '@/components/FormFields/BaseFormField.vue'

export default {
    components: {
        BaseFormField
    },
    props:['title', 'placeholder', 'value', 'error'],
    computed: {
        valueData: {
            get() {
                return this.value;
            },
            set(value) {
                this.$emit('input', value);
            }
        }
    }
}