<template>
    <aside class="filter">
        <h2 class="filter__title">Фильтры</h2>

        <form class="filter__form form" action="#" method="get" @submit.prevent="submit()">
          <fieldset class="form__block">
            <legend class="form__legend">Цена</legend>
            <label class="form__label form__label--price">
              <input class="form__input" type="text" name="min-price" v-model.number="currentFilterPriceFrom">
              <span class="form__value">От</span>
            </label>
            <label class="form__label form__label--price">
              <input class="form__input" type="text" name="max-price" v-model.number="currentFilterPriceTo">
              <span class="form__value">До</span>
            </label>
          </fieldset>

          <fieldset class="form__block">
            <legend class="form__legend">Категория</legend>
            <label class="form__label form__label--select">
              <select class="form__select" type="text" name="category" v-model.number="currentFilterCategoryId">
                <option value="0">Все категории</option>
                <option :value="category.id" v-for="category in categories" :key="category.id">{{category.title}}</option>                
              </select>
            </label>
          </fieldset>

          <fieldset class="form__block">
            <legend class="form__legend">Цвет</legend>
            <ul class="colors">
              <li class="colors__item" v-for="color in colors" :key="color.id">
                <label class="colors__label">
                  <input class="colors__radio sr-only" type="radio" :value="color.id" v-model.number="currentFilterColorId" >
                  <span class="colors__value" :style="{ backgroundColor: color.code}">
                  </span>
                </label>
              </li>  
            </ul>
          </fieldset>

          <fieldset class="form__block">
            <legend class="form__legend">Объем в ГБ</legend>
            <ul class="check-list">
              <li class="check-list__item">
                <label class="check-list__label">
                  <input class="check-list__check sr-only" type="checkbox" name="volume" value="8" checked="">
                  <span class="check-list__desc">
                    8 
                    <span>(313)</span>
                  </span>
                </label>
              </li>
              <li class="check-list__item">
                <label class="check-list__label">
                  <input class="check-list__check sr-only" type="checkbox" name="volume" value="16">
                  <span class="check-list__desc">
                    16 
                    <span>(461)</span>
                  </span>
                </label>
              </li>
              <li class="check-list__item">
                <label class="check-list__label">
                  <input class="check-list__check sr-only" type="checkbox" name="volume" value="32">
                  <span class="check-list__desc">
                    32 
                    <span>(313)</span>
                  </span>
                </label>
              </li>
              <li class="check-list__item">
                <label class="check-list__label">
                  <input class="check-list__check sr-only" type="checkbox" name="volume" value="64">
                  <span class="check-list__desc">
                    64 
                    <span>(313)</span>
                  </span>
                </label>
              </li>
              <li class="check-list__item">
                <label class="check-list__label">
                  <input class="check-list__check sr-only" type="checkbox" name="volume" value="128">
                  <span class="check-list__desc">
                    128 
                    <span>(313)</span>
                  </span>
                </label>
              </li>
              <li class="check-list__item">
                <label class="check-list__label">
                  <input class="check-list__check sr-only" type="checkbox" name="volume" value="264">
                  <span class="check-list__desc">
                    264 
                    <span>(313)</span>
                  </span>
                </label>
              </li>
            </ul>
          </fieldset>

          <button class="filter__submit button button--primery" type="submit">
            Применить
          </button>
          <button class="filter__reset button button--second" type="button" @click="reset()">
            Сбросить
          </button>
        </form>
      </aside>
</template>

<script>
// import categories from '../data/categories';
import axios from 'axios';

export default {
    props: [ 'filterPriceFrom', 'filterPriceTo', 'filterCategoryId', 'filterColorId' ],
    data() {
        return {       
            currentFilterPriceFrom: 0,
            currentFilterPriceTo: 0,
            currentFilterCategoryId: 0,
            currentFilterColorId: 0,
            categoriesData: null,
            colorsData: null
        };
    },
    computed: {
        categories() {
            // return categories;
            return this.categoriesData?.items ?? [];
        },
        colors() {
            return this.colorsData?.items ?? [];
        }
    },
    watch: {
        filterPriceFrom(value) {
            this.currentFilterPriceFrom = value;
        },
        filterPriceTo(value) {
            this.currentFilterPriceTo = value;
        },
        filterCategoryId(value) {
            this.currentFilterCategoryId = value;
        },
        filterColorId(value) {
            this.currentFilterColorId = value;
        }
    },
    methods: {
        submit() {
            this.$emit('update:filterPriceFrom', this.currentFilterPriceFrom);
            this.$emit('update:filterPriceTo', this.currentFilterPriceTo);
            this.$emit('update:filterCategoryId', this.currentFilterCategoryId);
            this.$emit('update:filterColorId', this.currentFilterColorId);
        },
        reset() {
            this.$emit('update:filterPriceFrom', 0);
            this.$emit('update:filterPriceTo', 0);
            this.$emit('update:filterCategoryId', 0);
            this.$emit('update:filterColorId', 0);
        },
        loadCategories() {
          axios.get('https://vue-study.skillbox.cc/api/productCategories')
            .then(response => this.categoriesData = response.data);
        },
        loadColors() {
          axios.get('https://vue-study.skillbox.cc/api/colors')
            .then(response => this.colorsData = response.data);
        }
    },
    created() {
      this.loadCategories();
      this.loadColors();
    }
}
</script>
