<template>
    <div>
        <router-link class="catalog__pic" :to="{name: 'product', params: { id: product.id }}">
            <img :src="product.image" :alt="product.title">
        </router-link>

        <h3 class="catalog__title">
            <a href="#">
            {{product.title}}
            </a>
        </h3>

        <span class="catalog__price">
            {{product.price | numberFormat}} р
        </span>

        <ul class="colors colors--black">
          <li class="colors__item" v-for="color in product.colors" :key="color.id">
            <label class="colors__label">
              <input class="colors__radio sr-only" type="radio" :value="color.code" v-model="currentColor" >
              <span class="colors__value" :style="{ backgroundColor: color.code}">
              </span>
            </label>
          </li>           
        </ul>
    </div>
    
</template>

<script>

// import gotoPage from '@/helpers/gotoPage';
import numberFormat from '@/helpers/numberFormat';

export default {
    props: ['product'],
    filters: {
      numberFormat
    },
    
    data() {
      return {
        currentColorData: null
      };      
    },
    computed: {
      firstColor: {
        get(){
          return this.product.colors[0].code;
        }
      },
      currentColor: {
        get() {
          return this.currentColorData ?? this.firstColor;
        },
        set(value) {
          this.currentColorData = value;
          console.log(value);
        }
      }
    }
    /* methods: {
      gotoPage
    } */
}

</script>
