<template>
    <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'cart'}">
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        {{$store.state.cartProducts.length}} товар{{($store.state.cartProducts.length > 1 ? 'а' : '')}}
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
        <div class="cart__field">
          <div class="cart__data">
            <form-text-field title="ФИО" :error="formErrors.name" placeholder="Введите ваше полное имя" 
              v-model="formData.name" name="name" />

            <form-text-field title="Адрес доставки" :error="formErrors.address" placeholder="Введите ваш адрес" 
              v-model="formData.address" name="address" />

            <form-text-field title="Телефон" type="tel" :error="formErrors.phone" placeholder="Введите ваш телефон" 
              v-model="formData.phone" name="phone" />

            <form-text-field title="Email" type="email" :error="formErrors.email" placeholder="Введите ваш Email" 
              v-model="formData.email" name="email" />

            <form-text-area-field title="Комментарий к заказу" :error="formErrors.comment" placeholder="Ваши пожелания" 
              v-model="formData.comment" name="comment" />
          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="0" v-model="delivery">
                  <span class="options__value">
                    Самовывоз <b>бесплатно</b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="500" v-model="delivery">
                  <span class="options__value">
                    Курьером <b>500 ₽</b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="Картой при получении" v-model="pay">
                  <span class="options__value">
                    Картой при получении
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="Наличными при получении" v-model="pay">
                  <span class="options__value">
                    Наличными при получении
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <li class="cart__order" v-for="item in products" :key="item.ProductId" :item="item">
              <h3>{{item.product.title}}</h3>
              <b>{{item.product.price | numberFormat}} ₽</b>
              <span>Артикул: {{item.product.id}}</span>
            </li>
          </ul>
          
          <div class="cart__total">
            <p>Доставка: <b>{{delivery | numberFormat}} ₽</b></p>
            <p>Итого: <b>{{$store.state.cartProducts.length}}</b> товара на сумму <b>{{totalPrice | numberFormat}} ₽</b></p>
          </div>

          <button class="cart__button button button--primery" type="submit">
            Оформить заказ
          </button>
        </div>
        <div class="cart__error form__error-block" v-if="formErrorMessage">
          <h4>Заявка не отправлена!</h4>
          <p>
            {{formErrorMessage}}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import numberFormat from '@/helpers/numberFormat'
import { mapGetters } from 'vuex'
import FormTextField from '@/components/FormFields/FormTextField.vue'
import FormTextAreaField from '@/components/FormFields/FormTextAreaField.vue'
import axios from 'axios'
import { API_BASE_URL } from '@/config'

export default {
  data() {
    return {
      formData: {},
      formErrors: {},
      formErrorMessage: '',
      pay: 'Картой при получении',
      delivery: 500
    };
  },
  components: {
    FormTextField,
    FormTextAreaField
  },
  filters: {
    numberFormat
  },
  computed: {
    ...mapGetters({ products: 'productDetails', totalPrice: 'cartTotalPrice' })    
  },
  methods: {
    order() {
      this.formErrors = {};
      this.formErrorMessage = '';

      axios.post(`${API_BASE_URL}/api/orders`, {
        ...this.formData
      }, {
        params: {
          userAccessKey: this.$store.state.userAccessKey
        }
      })
      .then(response => {
        this.$store.commit('resetCart');
        this.$store.commit('updateOrderInfo', {
          ...response.data, 
          pay: this.pay, 
          delivery: this.delivery
        });
        this.$router.push({name: 'orderInfo', params: { id: response.data.id }});
      })
      .catch(error => {        
        this.formErrors = error.response.data.error.request || {};
        this.formErrorMessage = error.response.data.error.message;
      });
    }
  }
}
</script>