<template>
    <div class="form__counter">
        <button type="button" aria-label="Убрать один товар" :disabled="amount === 1" @click="amount--">
            <svg :width="size" :height="size" fill="currentColor">
            <use xlink:href="#icon-minus"></use>
            </svg>
        </button>

        <input type="text" value="1" name="count" v-model.number="amount">

        <button type="button" aria-label="Добавить один товар" @click="amount++">
            <svg :width="size" :height="size" fill="currentColor">
            <use xlink:href="#icon-plus"></use>
            </svg>
        </button>
    </div>
</template>

<script>
export default {
    props: ['productAmount', 'size'],
    computed: {
        amount: {
            get() {
                return this.productAmount;
            },
            set(value) {
                this.$emit('update:productAmount', value);
            }
        }
    }
}
</script>