import Vue from "vue";
import Vuex from 'vuex'
// import products from '@/data/products'
import axios from 'axios'
import { API_BASE_URL } from '@/config'

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        cartProducts: [],
        cartProductData: [],
        userAccessKey: null,
        orderInfo: null
    },
    mutations: {
        // addProductToCart(state, payload) {
        //     const item = state.cartProducts.find(item => item.productId === payload.productId);            

        //     if (item) {
        //         item.amount += payload.amount;
        //     } else {
        //         state.cartProducts.push({ productId: payload.productId, amount: payload.amount });
        //     }
        // },
        updateOrderInfo(state, orderInfo) {
            state.orderInfo = orderInfo;
        },
        resetCart(state) {
            state.cartProducts = [];
            state.cartProductData = [];
        },
        updateCartProductAmount(state, { productId, amount }) {
            const item = state.cartProducts.find(item => item.productId === productId);

            if (item) {
                item.amount = amount;
            }
        },
        deleteCartProduct(state, productId) {
            state.cartProducts = state.cartProducts.filter(p => p.productId !== productId);
        },
        updateCartProductData(state, items) {
            state.cartProductData = items;
        },
        updateUserAccessKey(state, userKey) {
            state.userAccessKey = userKey;
        },
        syncCartProductData(state) {
            state.cartProducts = state.cartProductData.map(data => {
                return {
                    productId: data.product.id,
                    amount: data.quantity
                };
            });
        }
    },
    getters: {
        productDetails(state) {
            return state.cartProducts.map(item => {
                const product = state.cartProductData.find(p => p.product.id === item.productId).product;

                return {
                    ...item,
                    // product: products.find(p => p.id === item.productId)
                    product: {
                        ...product,
                        image: product.image.file.url
                    }
                };
            });
        },
        cartTotalPrice(state, getters) {
            return getters.productDetails.reduce((acc, item) => (item.product.price * item.amount) + acc, 0);
        }
    },
    actions: {
        loadOrderInfo(ctx, orderId) {
            return axios.get(`${API_BASE_URL}/api/orders/${orderId}`, {
                params: {
                    userAccessKey: ctx.state.userAccessKey
                }
            })
            .then(response => {
                ctx.state.orderInfo = response.data;
            });
        },
        loadCart(ctx) {
            return axios.get(`${API_BASE_URL}/api/baskets`, {
                params: {
                    userAccessKey: ctx.state.userAccessKey
                }
            })
            .then(response => {
                if (!ctx.state.userAccessKey) {
                    ctx.commit('updateUserAccessKey', response.data.user.accessKey);
                    localStorage.setItem('accessKey', response.data.user.accessKey);
                }
                ctx.commit('updateCartProductData', response.data.items); 
                ctx.commit('syncCartProductData');               
            });
        },
        addProductToCart(ctx, { productId, amount }) {
            return (new Promise(resolve => setTimeout(resolve, 2000)))
                .then(() => {
                    return axios.post(`${API_BASE_URL}/api/baskets/products`, {
                        productId: productId,
                        quantity: amount
                    }, {
                        params: {
                            userAccessKey: ctx.state.userAccessKey
                        }
                    })
                    .then(response => {
                        ctx.commit('updateCartProductData', response.data.items); 
                        ctx.commit('syncCartProductData'); 
                    });
                });            
        },
        updateCartProductAmount(ctx, { productId, amount }) {
            ctx.commit('updateCartProductAmount', { productId, amount });

            if (amount < 1)
                return;

            return axios.put(`${API_BASE_URL}/api/baskets/products`, {
                productId: productId,
                quantity: amount
            }, {
                params: {
                    userAccessKey: ctx.state.userAccessKey
                }
            })
            .then(response => {
                ctx.commit('updateCartProductData', response.data.items);               
            })
            .catch(() => {
                ctx.commit('syncCartProductData'); 
            });
        },
        deleteCartProduct(ctx, productId) {
            return axios.delete(`${API_BASE_URL}/api/baskets/products`, {
                params: {
                    userAccessKey: ctx.state.userAccessKey
                },
                data: {
                    productId
                }
            })
            .then(response => {
                ctx.commit('updateCartProductData', response.data.items);
                ctx.commit('syncCartProductData');                
            });
        }
    }
});
