<template>
    <a class="tabs__link" :class="{'tabs__link--current': currentTabNumber === id}" @click.prevent="setTabNumber">
        <slot/>
    </a>
</template>

<script>
export default {
    props: ['id', 'currentTabNumber'],
    methods: {
        setTabNumber() {
            this.$emit('update:currentTabNumber', this.id);
        }
    }
}
</script>