<template>
    <main class="content container" v-if="productLoading">Загрузка товара...</main>
    <main class="content container" v-else-if="!product || productLoadingFailed">Товар не загрузился</main>
    <main class="content container" v-else>
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <!-- <a class="breadcrumbs__link" href="#" @click.prevent="gotoPage('main')">
            Каталог
          </a> -->
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="#" @click.prevent="$router.push({name: 'main'})">
            {{category}}
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            {{product.title}}
          </a>
        </li>
      </ul>
    </div>

    <section class="item">
      <div class="item__pics pics">
        <div class="pics__wrapper">
          <img width="570" height="570" :src="product.image" :alt="product.title" />
        </div>
        <ul class="pics__list">
          <li class="pics__item">
            <a href="" class="pics__link pics__link--current">
              <img width="98" height="98" :src="product.image" :alt="product.title"/>
            </a>
          </li>          
        </ul>
      </div>

      <div class="item__info">
        <span class="item__code">Артикул: {{product.id}}</span>
        <h2 class="item__title">
          {{product.title}}
        </h2>
        <div class="item__form">
          <form class="form" action="#" method="POST" @submit.prevent="addToCart">
            <b class="item__price">
              {{product.price | numberFormat}} ₽
            </b>

            <fieldset class="form__block">
              <legend class="form__legend">Цвет:</legend>
              <ul class="colors">
                <li class="colors__item" v-for="color in product.colors" :key="color.id">
                  <label class="colors__label">
                    <input class="colors__radio sr-only" type="radio" :value="color.code" v-model="currentColor" >
                    <span class="colors__value" :style="{ backgroundColor: color.code}">
                    </span>
                  </label>
                </li>
              </ul>
            </fieldset>

            <fieldset class="form__block">
              <legend class="form__legend">Объемб в ГБ:</legend>

              <ul class="sizes sizes--primery">
                <li class="sizes__item">
                  <label class="sizes__label">
                    <input class="sizes__radio sr-only" type="radio" name="sizes-item" value="32">
                    <span class="sizes__value">
                      32gb
                    </span>
                  </label>
                </li>
                <li class="sizes__item">
                  <label class="sizes__label">
                    <input class="sizes__radio sr-only" type="radio" name="sizes-item" value="64">
                    <span class="sizes__value">
                      64gb
                    </span>
                  </label>
                </li>
                <li class="sizes__item">
                  <label class="sizes__label">
                    <input class="sizes__radio sr-only" type="radio" name="sizes-item" value="128" checked="">
                    <span class="sizes__value">
                      128gb
                    </span>
                  </label>
                </li>
              </ul>
            </fieldset>

            <div class="item__row">
              <product-amount-control :productAmount.sync="productAmount" :size="12" />

              <button class="button button--primery" type="submit" :disabled="productAdding">
                В корзину
              </button>
              <div v-show="productAdded">Товар добавлен</div>
              <div v-show="productAdding">Товар добавляется...</div>
            </div>
          </form>
        </div>
      </div>

      <div class="item__desc">
        <ul class="tabs">
          <li class="tabs__item" v-for="(title, idx) in tabDescriptions" :key="idx">
            <tab-item :currentTabNumber.sync="currentTabNumber" :id="idx">{{title}}</tab-item>
          </li>
        </ul>

        <div class="item__content" v-if="currentTabNumber === 0">
          <p>
            Навигация GPS, ГЛОНАСС, BEIDOU Galileo и QZSS<br>
            Синхронизация со смартфоном<br>
            Связь по Bluetooth Smart, ANT+ и Wi-Fi<br>
            Поддержка сторонних приложений<br>
          </p>
  
          <a href="#" @click.prevent="currentTabNumber = 1">
            Все характеристики
          </a>
  
          <h3>Что это?</h3>
  
          <p>
            Wahoo ELEMNT BOLT GPS – это велокомпьютер, который позволяет оптимизировать свои велотренировки, сделав их максимально эффективными. Wahoo ELEMNT BOLT GPS синхронизируется с датчиками по ANT+, объединяя полученную с них информацию. Данные отображаются на дисплее, а также сохраняются на смартфоне. При этом на мобильное устройство можно установить как фирменное приложение, так и различные приложения сторонних разработчиков. Велокомпьютер точно отслеживает местоположение, принимая сигнал с целого комплекса спутников. Эта информация позволяет смотреть уже преодоленные маршруты и планировать новые велопрогулки.
          </p>
  
          <h3>Дизайн</h3>
  
          <p>
            Велокомпьютер Wahoo ELEMNT BOLT очень компактный. Размеры устройства составляют всего 74,6 x 47,3 x 22,1 мм. что не превышает габариты смартфона. Корпус гаджета выполнен из черного пластика. На обращенной к пользователю стороне расположен дисплей диагональю 56 мм. На дисплей выводятся координаты и скорость, а также полученная со смартфона и синхронизированных датчиков информация: интенсивность, скорость вращения педалей, пульс и т.д. (датчики не входят в комплект поставки). Корпус велокомпьютера имеет степень защиты от влаги IPX7. Это означает, что устройство не боится пыли, а также выдерживает кратковременное (до 30 минут) погружение в воду на глубину не более 1 метра.
          </p>
        </div>

        <div class="item__content" v-if="currentTabNumber === 1">
            <h3>Беспроводная связь</h3>
            <p>Wi-Fi, Bluetooth 4.0, ANT+</p> 

            <h3>Поддерживаемые спутники</h3>
            <p>GPS, ГЛОНАСС, BEIDOU Galileo и QZSS</p>

            <h3>Аккумулятор</h3>
            <p>перезаряжаемый USB</p>

            <h3>Время автономной работы</h3>
            <p>до 15 часов</p>

            <h3>Тип аккумулятора</h3>
            <p>литий-ионный</p>

            <h3>Водонепроницаемость</h3>
            <p>IPX7</p>

            <h3>Фирменное приложение</h3>
            <p>ELEMNT от Wahoo Fitness</p> 

            <h3>Совместимость</h3>
            <p>iOS 9.0 и последующие, Android 4.3 и старше</p>
        </div>
      </div>
    </section>
  </main>
</template>

<script>
import gotoPage from '@/helpers/gotoPage';
import numberFormat from '@/helpers/numberFormat';
// import categories from '@/data/categories';
// import products from '@/data/products';
import axios from 'axios';
import { API_BASE_URL } from '@/config';
import { mapActions } from 'vuex';
import ProductAmountControl from '@/components/ProductAmountControl.vue';
import TabItem from '@/components/TabItem.vue';

export default {
  // props: ['pageParams'],
  components: {
    ProductAmountControl,
    TabItem
  },
  data() {
    return {
      productAmount: 1,
      productData: null,
      productLoading: false,
      productLoadingFailed: false,
      productAdded: false,
      productAdding: false,
      currentColorData: null,
      currentTabNumber: 0,
      tabDescriptions: ['Описание', 'Характеристики', 'Гарантия', 'Оплата и доставка']
    };    
  },
  filters: {
    numberFormat
  },
  methods: {
    ...mapActions(['addProductToCart']),
    gotoPage,
    addToCart() {      
      //this.$store.commit('addProductToCart', { productId: this.product.id, amount: this.productAmount });
      this.productAdded = false;
      this.productAdding = true;
      this.addProductToCart({ productId: this.product.id, amount: this.productAmount })
        .then(() => {
          this.productAdded = true;
          this.productAdding = false;
        });
    },
    loadProduct() {
      this.productLoading = true;
      this.productLoadingFailed = false;

      axios.get(`${API_BASE_URL}/api/products/${this.$route.params.id}`)
        .then(response => this.productData = response.data)
        .catch(() => this.productLoadingFailed = true)
        .then(() => this.productLoading = false);
    }
  },
  computed: {
    category() {
      // return categories.find(c => c.id === this.product.categoryId).title;
      return this.productData.category.title;
    },
    product() {
      // return products.find(p => p.id === +this.$route.params.id);
      return {
          ...this.productData,
          image: this.productData.image.file.url
        };
    },
    firstColor: {
      get(){
        return this.product.colors[0].code;
      }
    },
    currentColor: {
      get() {
        return this.currentColorData ?? this.firstColor;
      },
      set(value) {
        this.currentColorData = value;
        console.log(value);
      }
    }
  },
  watch: {
    '$route.params.id': {
      handler() {
        this.loadProduct();
      },
      immediate: true
    }
  }
}
</script>